import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import classNames from 'classnames';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import UserTable from './UserTable';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import {base_url, public_url, $http} from "config";
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';

const drawerWidth = 240;

const btn_rows = [
  {id: 0, icon: 'zmdi-view-list', content: 'All'},
  {id: 1, icon: 'zmdi-disqus', content: 'CDEC'},
]
const btn_rows1 = [
  {id: 2, icon: 'zmdi-assignment-check', content: 'Active'},
  {id: 3, icon: 'zmdi-assignment-alert', content: 'In Active'},
]

const styles = theme => ({
  root: {
    width: '100%',
    height: '100%',
    zIndex: 1,
    overflow: 'hidden',
  },
  appFrame: {
    position: 'relative',
    display: 'flex',
    width: '100%',
    height: '100%',
  },
  appBar: {
    position: 'absolute',
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawerPaper: {
    position: 'relative',
    height: '100%',
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  content: {
    width: '100%',
    marginLeft: -drawerWidth,
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginTop: 56,
    [theme.breakpoints.up('sm')]: {
      content: {
        height: 'calc(100% - 64px)',
        marginTop: 64,
      },
    },
  },
  contentShift: {
    marginLeft: 0,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
});

class PersistentUser extends React.Component {
  state = {
    open: false,
    filter_kind: 0,
    loader : false,
    goback : false,
    alldata: [],
    customers: [],
    users: [],
    filter_id :0,
  };

  handleDrawerOpen = () => {
    this.setState({open: true});
  };

  handleDrawerClose = () => {
    this.setState({open: false});
  };

  init_Data = () => {
    $http.get(base_url + "user/totalUserlist")
    .then((response) => {
      this.setState({
        users: response.data.users,
        alldata:response.data.users,
        customers: response.data.customers,
        loader: true
      })
    })
    .catch((error) => {
      this.setState({goback: true})
    })
  }

  setCustomer = (value) => {
  }

  setUser = (value) => {
    this.setState({user_id: value})
  }

  filter = (kind,index) => {
    if(kind==1)
      this.setState({filter_kind:index,filter_id:0})
    else
      this.setState({filter_kind:-1,filter_id:index})
    
    this.setState({users: this.state.alldata.filter((item) => {
      return ((kind == 1) && (index == 0 ||
        (index == 1 && item.company_id == 1) ||
        (index == 2 && item.status == 1) ||
        (index == 3 && item.status == 0)) ||
        kind == 2 && (index == 0 || item.company_id == index))
    })})
  }

  componentDidMount = () => {
    this.init_Data()
  }
  

  render() {
    const {classes, theme} = this.props;
    const nowuser = JSON.parse(localStorage.getItem("user"));
    return (
      <div className={classes.root}>
        <div className={classes.appFrame}>
          <AppBar className={classNames('bg-primary', classes.appBar, this.state.open && classes.appBarShift)}>
            <Toolbar disableGutters={!this.state.open}>
              <IconButton
                aria-label="open drawer"
                onClick={this.handleDrawerOpen}
                className={classNames(classes.menuButton, this.state.open && classes.hide)}>

                <MenuIcon className="text-white"/>
              </IconButton>
              <Typography variant="h6" color="inherit" className="text-white" noWrap>
                Users
              </Typography>
            </Toolbar>
          </AppBar>
          <Drawer
            variant="persistent"
            classes={{
              paper: classes.drawerPaper,
            }}
            open={this.state.open}
          >
            <div className={classes.drawerInner}>
              <div className={classes.drawerHeader}>
                <IconButton onClick={this.handleDrawerClose}>
                  {theme.direction === 'rtl' ? <ChevronRightIcon/> : <ChevronLeftIcon/>}
                </IconButton>
              </div>
              <Divider/>
              <List className={classes.list}>
                {
                btn_rows.map((item) => {
                    return  <ListItem button 
                                className="py-0"
                                key={item.id}
                                onClick={() => this.filter(1,item.id)}>
                                <ListItemIcon>
                                <IconButton className={`icon-btn ${this.state.filter_kind == item.id ? 'text-primary' : ''}`}>
                                    <i className={`zmdi ${item.icon} zmdi-hc-fw`}></i>
                                </IconButton>
                                </ListItemIcon>
                                <ListItemText primary={item.content} className={`${this.state.filter_kind == item.id ? 'text-primary' : ''}`}/>
                            </ListItem>
                  })
                }
                <ListItem className="module-nav-label">
                    <TextField
                        id="filter"
                        select
                        label="customer"
                        value={this.state.filter_id}
                        onChange={(event)=>{this.filter(2,event.target.value)}}
                        margin="normal"
                        fullWidth
                    >
                      <MenuItem key={0} value={0}>
                            All
                      </MenuItem>
                      {this.state.customers.map(option => (
                          <MenuItem key={option.id} value={option.id}>
                              {option.name}
                          </MenuItem>
                      ))}
                    </TextField>
                </ListItem>

                {
                btn_rows1.map((item) => {
                    return  <ListItem button 
                                className="py-0"
                                key={item.id}
                                onClick={() => this.filter(1,item.id)}>
                                <ListItemIcon>
                                <IconButton className={`icon-btn ${this.state.filter_kind == item.id ? 'text-primary' : ''}`}>
                                    <i className={`zmdi ${item.icon} zmdi-hc-fw`}></i>
                                </IconButton>
                                </ListItemIcon>
                                <ListItemText primary={item.content} className={`${this.state.filter_kind == item.id ? 'text-primary' : ''}`}/>
                            </ListItem>
                  })
                }
              </List>
            </div>
          </Drawer>
          <main className={classNames(classes.content, this.state.open && classes.contentShift)}>
            <UserTable 
              users={this.state.users}
              loader={this.state.loader}
              goback={this.state.goback}
              init_Data = {this.init_Data}
              />
          </main>
        </div>
      </div>
    );
  }
}

PersistentUser.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, {withTheme: true})(PersistentUser);